[data-component-sorts='BubbleSort'],
[data-component-sorts='SelectionSort'] {
	margin-top: 20px;
	text-align: center;
	display: flex;
	align-items: center;
	flex-direction: column;
}

@media screen and (max-width: 600px) {
	*[data-component-sorts] {
		scale: 0.8;
	}
}
@media screen and (max-width: 500px) {
	*[data-component-sorts] {
		scale: 0.7;
	}
}
button {
	background-color: #a04caf; /* Green */
	border: none;
	color: white;
	padding: 10px 15px;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	font-size: 20px;
	margin: 4px 2px;
	cursor: pointer;
	border-radius: 10px;
}

button:hover {
	background-color: #8e3e82;
}
.button--active {
	border: 2px dotted white;
}

.button-container {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 20px;
}

.bar-container {
	display: flex;
	align-items: center;
}

.bar {
	transition: 100ms;
	color: white;
	font-size: 20px;
	width: 30px;
	height: 20px;
	background: #39ff14;
	margin: 2px;
	display: flex;
	align-items: flex-start;
	justify-content: space-evenly;
}

.bar .text {
	margin: -25px;
}

.animation-speed-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin-top: 20px;
}

/* Slider label */
.animation-speed-container label {
	margin-bottom: 10px;
}

/* Slider styles */
.animation-speed-slider {
	-webkit-appearance: none;
	width: 100%;
	height: 10px;
	border-radius: 5px;
	background: #d3d3d3;
	outline: none;
	opacity: 0.7;
	-webkit-transition: 0.2s;
	transition: opacity 0.2s;
}

.animation-speed-slider:hover {
	opacity: 1;
}

.animation-speed-slider::-webkit-slider-thumb {
	-webkit-appearance: none;
	appearance: none;
	width: 20px;
	height: 20px;
	border-radius: 50%;
	background: #a04caf;
	cursor: pointer;
}

.animation-speed-slider::-moz-range-thumb {
	width: 20px;
	height: 20px;
	border-radius: 50%;
	background: #a04caf;
	cursor: pointer;
}

.loader-message {
	font-size: 15px;
	margin-top: 15px;
	border-bottom: 1px dotted #39ff14;
	padding: 5px;
}
